import React, { Component } from 'react'
import { Input, Button, message } from 'antd'
import api from '@/utils/api'

class Login extends Component {
	constructor(props) {
		super(props)
		this.state = {
			username: 'zhangsan',
			password: '123'
		}
	}
	submitHandle() {
		api.userLogin(this.state).then(res => {
			console.log('登陆信息', res)
			message.success('登陆成功')
			this.props.router.push(`/blog/list`)
		})
	}
	changeValue(ev, key) {
		this.state[key] = ev.target.value
	}
	render() {
		return (
			<div className="common-page login-page">
				<h3>Login</h3>
				<div className="form-line">
					<span>名称：</span>
					<Input placeholder="username" onChange={ev => this.changeValue(ev, 'username')} />
				</div>
				<div className="form-line">
					<span>密码：</span>
					<Input placeholder="password" onChange={ev => this.changeValue(ev, 'password')} />
				</div>
				<div>
					<Button onClick={() => this.submitHandle()}>提交</Button>
				</div>
				<h3>先启动redis</h3>
			</div>
		)
	}
}
export default Login



